@import "../../../assets/scss/app";

.buyWebsiteModal {
  z-index: 999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: vw(450);
  background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
  box-shadow: 15px 10px 20px rgba(0, 0, 0, 0.1), inset 0px -2px 1px rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(30px);
  border-radius: vw(7);
  padding: vw(55) vw(35) vw(54);
  display: none;

  @include tablet() {
    width: 450px;
    border-radius: 7px;
    padding: 55px 35px 54px;
  }

  &_show {
    display: block;
  }

  .close {
    @include button();
    width: vw(24);
    height: vw(24);
    position: absolute;
    top: vw(14);
    right: vw(14);

    @include tablet() {
      width: 24px;
      height: 24px;
      top: 14px;
      right: 14px;
    }

    svg {
      width: vw(24);
      height: vw(24);

      @include tablet() {
        width: 24px;
        height: 24px;
      }
    }
  }

  img {
    width: vw(320);
    height: vw(313);
    display: block;
    margin: 0 auto;

    @include tablet() {
      width: 320px;
      height: 313px;
    }
  }

  .title {
    margin-top: vw(30);
    font-weight: 500;
    font-size: vw(33);
    line-height: vw(40);

    @include tablet() {
      margin-top: 30px;
      font-size: 33px;
      line-height: 40px;
    }
  }

  .priceWrapper {
    margin-top: vw(7);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include tablet() {
      margin-top: 7px;
    }

    p {
      font-weight: bold;
      font-size: vw(40);
      line-height: vw(49);

      @include tablet() {
        font-size: 40px;
        line-height: 49px;
      }
    }

    .btn {
      cursor: pointer;
      user-select: none;
      border: 1px solid rgba(131, 131, 131, 0.3);
      box-sizing: border-box;
      border-radius: vw(8);
      padding: vw(13) vw(45);
      font-weight: 500;
      font-size: vw(16);
      line-height: vw(20);

      @include tablet() {
        border-radius: 8px;
        padding: 13px 45px;
        font-size: 16px;
        line-height: 20px;
      }
    }
  }

  .text {
    margin-top: vw(28);
    font-weight: 500;
    font-size: vw(16);
    line-height: vw(20);

    @include tablet() {
      margin-top: 28px;
      font-size: 16px;
      line-height: 20px;
    }
  }

  .field {
    margin-top: vw(30);

    @include tablet() {
      margin-top: 30px;
    }
  }

  .submitBtn {
    margin: vw(48) auto 0;

    @include tablet() {
      margin: 48px auto 0;
    }
  }
}