@import "../../../assets/scss/app";

.onSubmitModal {
  z-index: 999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: vw(100) vw(35) vw(40);
  background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
  box-shadow: 15px 10px 20px rgba(0, 0, 0, 0.1), inset 0px -2px 1px rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(30px);
  border-radius: vw(7);
  display: none;

  @include tablet() {
    padding: 100px 35px 40px;
    border-radius: 7px;
  }

  &_show {
    display: block;
  }

  .close {
    @include button();
    width: vw(24);
    height: vw(24);
    position: absolute;
    top: vw(14);
    right: vw(14);

    @include tablet() {
      width: 24px;
      height: 24px;
      top: 14px;
      right: 14px;
    }

    svg {
      width: vw(24);
      height: vw(24);

      @include tablet() {
        width: 24px;
        height: 24px;
      }
    }
  }

  .title {
    font-weight: 500;
    font-size: vw(33);
    line-height: vw(40);
    text-align: center;

    @include tablet() {
      font-size: 33px;
      line-height: 40px;
    }
  }

  .btn {
    margin: vw(40) auto 0;

    @include tablet() {
      margin: 40px auto 0;
    }

  }

}